/* -------------------------------------------------------------------------- 
	Doc setup
  -------------------------------------------------------------------------- */
*,
*:after,
*:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

:root {
  font-size: 16px;
}

html {
  font-family: var(--xe_system_font_family);
  line-height: 1.15;
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
  width: device-width;
}
body {
  padding: 0;
  margin: 0;
  font-weight: var(--xe_base_font_weight);
  font-size: var(--xe_base_font_size);
  color: var(--xe_base_text_color);
  font-family: var(--xe_base_font_family);
  line-height: var(--xe_base_font_line_height);
  letter-spacing: var(--xe_base_letter_spacing);
  background-color: var(--xe_body_background_color);
  -moz-word-wrap: break-word;
  -webkit-word-wrap: break-word;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

/* -------------------------------------------------------------------------- 
	Headings
  -------------------------------------------------------------------------- */
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: var(--xe_heading_line_height);
  font-family: var(--xe_heading_font_family);
  font-weight: var(--xe_heading_font_weight);
  letter-spacing: var(--xe_heading_letter_spacing);
  color: var(--xe_base_heading_color);
  margin-top: 20px;
  margin-bottom: 30px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

h1 {
  font-size: var(--xe_heading_h1_font_size);
  line-height: 1.1;
}

h2 {
  font-size: var(--xe_heading_h2_font_size);
  line-height: 1.2;
}

h3 {
  font-size: var(--xe_heading_h3_font_size);
  line-height: 1.3;
}

h4 {
  font-size: var(--xe_heading_h4_font_size);
  line-height: 1.4;
}

h5 {
  font-size: var(--xe_heading_h5_font_size);
  line-height: 1.5;
}

h6 {
  font-size: var(--xe_heading_h6_font_size);
  line-height: 1.6;
}

/* -------------------------------------------------------------------------- 
	Strong, Bold, Big, Small, Sup & Sub
  -------------------------------------------------------------------------- */
b,
strong {
  font-weight: bold;
}

big {
  font-size: 125%;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 80%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

i,
em {
  font-style: italic;
}

em {
  letter-spacing: 0;
}

ins,
mark {
  color: var(--xe_base_text_color);
  background-color: #fffbae;
  text-decoration: none;
}

s,
del,
strike {
  text-decoration: line-through;
  background-color: transparent;
}

bdi {
  background-color: transparent;
}

/* -------------------------------------------------------------------------- 
	Responsive
   -------------------------------------------------------------------------- */
@media (max-width: 575px) {
  h1 {
    font-size: calc(var(--xe_heading_h1_font_size) - 25%);
  }
  h2 {
    font-size: calc(var(--xe_heading_h2_font_size) - 20%);
  }
  h3 {
    font-size: calc(var(--xe_heading_h3_font_size) - 15%);
  }
  h4 {
    font-size: calc(var(--xe_heading_h4_font_size) - 10%);
  }
  h5 {
    font-size: calc(var(--xe_heading_h5_font_size) - 5%);
  }
  h6 {
    font-size: calc(var(--xe_heading_h6_font_size));
  }
}
/* -------------------------------------------------------------------------- 
    Text & Anchor
  -------------------------------------------------------------------------- */
p {
  margin: 0 0 20px 0;
}

p:empty {
  display: none;
}

a {
  color: var(--xe_base_link_color);
  text-decoration: none;
  transition: all 0.5s ease;
}

a:hover {
  color: var(--xe_base_link_hover_color);
  text-decoration: underline;
}

a:focus {
  outline: 1px dotted currentColor;
  text-decoration: none;
}

a:not([href]):not([tabindex]):not(.wp-block-button__link) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):not(.wp-block-button__link):hover,
a:not([href]):not([tabindex]):not(.wp-block-button__link):focus {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

[tabindex="-1"]:focus {
  outline: 0 !important;
}

.wp-block-button__link,
.wp-block-button__link:hover {
  text-decoration: none;
}

hr {
  height: 1px;
  border: none;
  padding: 0;
  margin: 20px 0;
  overflow: hidden;
  box-sizing: border-box;
  background-color: var(--xe_border_color);
}

hr.wp-block-separator {
  max-width: 150px;
  margin: 20px auto;
}

hr.wp-block-separator.is-style-wide {
  max-width: 100%;
}

.wp-block-separator.is-style-dots {
  background-color: none;
  border: none;
  text-align: center;
  max-width: none;
  line-height: 1;
  height: auto;
}

/* --------------------------------------------------------------------------
	Forms default
  -------------------------------------------------------------------------- */
form {
  display: block;
  margin-bottom: 30px;
}

form .form-group {
  margin-bottom: 20px;
}

form .form-group:last-child {
  margin-bottom: 0;
}

fieldset {
  margin: 0;
  padding: 20px 30px;
  border: 1px solid var(--xe_border_color);
  border-radius: 5px;
}

legend {
  display: table;
  font-size: 15px;
  font-size: var(--xe_base_font_size);
  font-weight: normal;
  padding: 0 20px 0 10px;
}

legend::after,
legend::before {
  content: "";
  clear: both;
}

label {
  display: inline-block;
  font-size: calc(var(--xe_base_font_size) - 1px);
  letter-spacing: var(--xe_base_letter_spacing);
  font-weight: normal;
  margin: 0 0 5px 5px;
}

label.inline {
  display: inline-flex;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

textarea,
input[type=text],
input[type=email],
input[type=url],
input[type=date],
input[type=month],
input[type=time],
input[type=week],
input[type=number],
input[type=search],
input[type=tel],
input[type=color],
input[type=datetime],
input[type=password],
input[type=datetime-local] {
  display: block;
  font-family: var(--xe_base_font_size);
  border: 1px solid var(--xe_inputs_border_color);
  font-size: 15px;
  margin: 0;
  padding: 14px 15px;
  height: 48px;
  width: 100%;
  max-width: 100%;
  border-radius: var(--xe_inputs_border_radius);
  -webkit-box-shadow: inset 1px 1px 3px transparent;
  -moz-box-shadow: inset 1px 1px 3px transparent;
  box-shadow: inset 1px 1px 3px transparent;
}

select:focus,
textarea:focus,
