/* -------------------------------------------------------------------------- 
	Table defaults
  -------------------------------------------------------------------------- */
table {
  border-collapse: collapse;
  border-spacing: 0;
  border-width: 0;
  padding: 0;
  margin: 0 0 30px 0;
  width: 100%;
  background: transparent;
  background-color: transparent;
}

table.fixed {
  table-layout: fixed;
}

th,
td {
  padding: 10px 15px;
  text-align: left;
  border-width: 1px;
  border-style: solid;
