}

/* -------------------------------------------------------------------------- 
	Pre & Code
  -------------------------------------------------------------------------- */
pre,
code,
kbd,
tt,
var,
samp {
  font-family: var(--xe_monospace_font_family);
  letter-spacing: 0;
}

pre {
  display: block;
  font-size: 15px;
  color: #FFFFFF;
  background-color: #2C2848;
  overflow: auto;
  max-width: 100%;
  font-weight: normal;
  padding: 30px;
  margin-bottom: 30px;
