}

.theme-xenial[data-title*=link-style-1] a:hover::after {
  content: "";
  background-color: red;
}

.theme-xenial[data-title*=link-style-2] a {
  color: blue;
}

.theme-xenial[data-title*=link-style-2] a::before,
.theme-xenial[data-title*=link-style-2] a::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: blue;
}

