/* -------------------------------------------------------------------------- 
	Float Alignments
  -------------------------------------------------------------------------- */
.alignleft,
.wp-block-image .alignleft {
  display: table;
  float: left;
  margin: 0 30px 30px 0;
}

.alignright,
.wp-block-image .alignright {
  display: table;
  float: right;
  margin: 0 0 30px 30px;
}

.alignleft::before,
.alignleft::after,
.alignright::before,
.alignright::after {
  content: "";
