}

.alignleft::after,
.alignright::after {
  content: "";
  clear: both;
}

.aligncenter {
  display: table;
  float: none;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

/* -------------------------------------------------------------------------- 
	Wide & Full
  -------------------------------------------------------------------------- */
.alignwide,
.alignfull {
  width: 100%;
  max-width: 100%;
  transform: none;
  margin-left: auto;
  margin-right: auto;
}

body:not([class*=xe-sidebar-position-]) .alignwide,
body:not([class*=xe-sidebar-position-]) .alignfull {
  position: relative;
  width: 80vw;
  max-width: 80vw;
  margin-right: auto;
  margin-left: 50%;
  transform: translateX(-50%);
}

