	WordPress Embeds
  -------------------------------------------------------------------------- */
.wp-block-embed.alignwide:not(.is-type-rich) .wp-block-embed__wrapper::before,
.wp-block-embed.alignfull:not(.is-type-rich) .wp-block-embed__wrapper::before,
.wp-block-embed.wp-embed-aspect-4-3:not(.is-type-rich) .wp-block-embed__wrapper::before {
  content: "";
