  padding-top: 56.25%;
}

.wp-block-embed.alignwide.wp-embed-aspect-4-3:not(.is-type-rich) .wp-block-embed__wrapper::before,
.wp-block-embed.alignfull.wp-embed-aspect-4-3:not(.is-type-rich) .wp-block-embed__wrapper::before {
  content: "";
  padding-top: 75%;
}

.wp-block-embed.alignwide:not(.is-type-rich) iframe,
.wp-block-embed.alignfull:not(.is-type-rich) iframe {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  max-width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 5px;
}

.wp-block-embed.alignwide.is-type-rich iframe,
.wp-block-embed.alignfull.is-type-rich iframe {
  width: 100%;
  height: auto;
  max-width: 100%;
}

.wp-embed-responsive .wp-block-embed.aligncenter {
