  font-size: 13px;
  line-height: 21px;
  font-weight: normal;
  padding: 4px 10px 4px 10px;
  border-radius: 2px;
}

.tippy-box[data-animation=scale][data-placement^=top] {
  transform-origin: bottom;
}

