  transform: scale(0.5);
  opacity: 0;
}

/* -------------------------------------------------------------------------- 
	Helper: Text Underline
  -------------------------------------------------------------------------- */
.has-text-underline {
  position: relative;
  text-decoration: none;
}

.has-text-underline:hover {
  text-decoration: none;
}

.has-text-underline:focus {
  outline: none;
  text-decoration: none;
}

.has-text-underline::after,
.has-text-underline::before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: currentColor;
}

.has-text-underline::before {
  content: "";
  z-index: 1;
  background-color: currentColor;
  transform-origin: right center;
  transform: scaleX(0);
  will-change: scale;
  transition: transform 0.26s cubic-bezier(0.25, 0.8, 0.25, 1);
  -webkit-backface-visibility: hidden;
}

.has-text-underline::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 3px;
  opacity: 0.5;
  background-color: currentColor;
}

.has-text-underline:hover::before {
  transform: scale(1);
  transform-origin: left center;
  background-color: var(--xe_primary_color);
}

