@keyframes slideInright {
  from {
    left: -100vw;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
/* -------------------------------------------------------------------------- 
	Animation SlideOutRight
  -------------------------------------------------------------------------- */
@keyframes slideoutright {
  from {
    left: 0;
    opacity: 1;
  }
  to {
    left: -100vw;
    opacity: 0;
  }
}
/* -------------------------------------------------------------------------- 
	Ajax WooCommerce search loading
  -------------------------------------------------------------------------- */
@keyframes wooAjaxSearchLoading {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 10px);
  }
  100% {
    transform: translate(0, 0);
  }
}